.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 3rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  padding: 0px;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}
.profile-image {
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 200px;
  
}
    @-webkit-keyframes example {
      0%   {opacity: .1;}
      100% {opacity: 1;}
    }
    @keyframes example {
      0%   {opacity: .1;}
      100% {opacity: 1;}
    }



